
import { _decorator, Component, Node, Collider2D, Contact2DType, IPhysics2DContact } from 'cc';
import { Player } from './Player';
const { ccclass, property } = _decorator;

 
@ccclass('ShieldAct')
export class ShieldAct extends Component {

    curActiveTime: number = 0;
    activeTime: number = 5;

    update (deltaTime: number) {
        
        if (this.node.parent.getComponent(Player).shieldActive)
        {
            this.curActiveTime += deltaTime;
            if (this.curActiveTime > this.activeTime)
            {
                this.node.active = false;
                this.node.parent.getComponent(Player).SetNonActiveShield();
                this.curActiveTime = 0;
            }
        }
    }

    SetActiveShield()
    {
        this.node.active = true;
        this.node.parent.getComponent(Player).SetActiveShield();
    }
}

